/* App.css */
body {
  margin: 0;
  font-family: 'Arial', sans-serif;
  background: linear-gradient(135deg, #f5f7fa, #c3cfe2);
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
}

.app {
  width: 100%;
  max-width: 600px;
  background: #ffffff;
  border-radius: 16px;
  box-shadow: 0 8px 24px rgba(0, 0, 0, 0.1);
  overflow: hidden;
  display: flex;
  flex-direction: column;
}

.chat-container {
  display: flex;
  flex-direction: column;
  height: 100%;
}

.output-area {
  flex: 1;
  padding: 20px;
  overflow-y: auto;
  background: #f9f9f9;
  border-bottom: 1px solid #e0e0e0;
}

.output-area > div {
  margin-bottom: 20px;
}

.output-area label {
  font-weight: bold;
  color: #333;
  display: block;
  margin-bottom: 8px;
  font-size: 14px;
}

.output-area p {
  margin: 0;
  color: #555;
  font-size: 16px;
  line-height: 1.5;
}

.language-selector {
  margin: 20px 0;
  padding: 10px;
  border: 1px solid #ddd;
  border-radius: 8px;
  background: #fff;
  font-size: 14px;
  color: #333;
  outline: none;
  transition: border-color 0.3s ease;
}

.language-selector:focus {
  border-color: #007bff;
}

button {
  padding: 10px 20px;
  background: #007bff;
  color: #fff;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  font-size: 14px;
  transition: background 0.3s ease, transform 0.2s ease;
}

button:hover {
  background: #0056b3;
  transform: translateY(-2px);
}

button:disabled {
  background: #ccc;
  cursor: not-allowed;
  transform: none;
}

.error {
  color: #ff4d4f;
  margin-top: 10px;
  font-size: 14px;
}

.text-input {
  display: flex;
  padding: 20px;
  background: #f9f9f9;
  border-top: 1px solid #e0e0e0;
}

.text-input textarea {
  flex: 1;
  padding: 10px;
  border: 1px solid #ddd;
  border-radius: 8px;
  resize: none;
  margin-right: 10px;
  font-size: 14px;
  color: #333;
  outline: none;
  transition: border-color 0.3s ease;
}

.text-input textarea:focus {
  border-color: #007bff;
}

.text-input button {
  padding: 10px 20px;
  background: #28a745;
  color: #fff;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  font-size: 14px;
  transition: background 0.3s ease, transform 0.2s ease;
}

.text-input button:hover {
  background: #218838;
  transform: translateY(-2px);
}

/* Responsive Design */
@media (max-width: 600px) {
  .app {
    width: 100%;
    height: 100vh;
    border-radius: 0;
  }

  .output-area {
    padding: 15px;
  }

  .text-input {
    padding: 15px;
  }
}